.productContainer {
    margin-top: 40px;
    margin-left: 40px;
    margin-right:30px ;
}

.productGrid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(400px,1fr));
    gap: 20px;
}
.product{
    border: 1px solid gray;
    border-radius: 12px;
    padding: 16px;
    text-align: center;
    background-color: white;
    transition: all 0.3 ease-in;
}
.product:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
}
.productImageDiv{
    width: 100%;
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: 8px;
}

.productImage{
    object-fit: cover;
}

.productName {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 8px;
    color: #333;
}

.description {
    color: #666;
    margin-bottom: 8px;
    line-height: 1.4;
}

.price {
    font-size: 20px;
    font-weight: 700;
    color: #2c5aa0;
}